button {
  color: white;
  border: none;
  outline: none;
  font-family: 'Share Tech Mono', sans-serif;
  font-size: 1.3em;

  &:active {
    box-shadow: 0 0 2px 1px rgba(255, 255, 255, 1);
  }
}

#clear {
  background: rgb(172, 57, 57);
  grid-column: 1 / 3;
}

#equals {
  background: rgb(0, 68, 102);
  grid-column: 4/5;
  grid-row: 5/ 7;
}

#zero {
  grid-column: 1 / 3;
}

.operator {
  background: rgb(102, 102, 102);
}

.number {
  background: #4d4d4d;
}


