.Calculator {
  display: grid;
  grid-template: auto repeat(5, 65px) / repeat(4, 80px);
  grid-gap: 1px;
  padding: 5px;
  background: black;
}

#screen {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  grid-column: 1 / 5;
  min-height: 3.5em;
  font-family: Digital, sans-serif;
  text-align: end;
  margin: 2px 0;

  #total {
    color: orange;
    font-size: 1.6em;
    overflow-wrap: break-word;
  }

  #display {
    color: white;
    font-size: 1.7em;
  }
}


