@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
@font-face {
    font-family: Digital;
    src: url(/typescript_calculator/static/media/digital-7.93911ab1.ttf);
}

:root {
    --app-height: 100vh;
    --app-width: 100vw;
}



body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
    height: 100vh;
    height: var(--app-height);
    width: 100vw;
    width: var(--app-width);
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    background: #c2c2d6;
}

.Calculator{display:grid;grid-template:auto repeat(5, 65px)/repeat(4, 80px);grid-gap:1px;padding:5px;background:black}#screen{display:flex;justify-content:space-between;flex-direction:column;grid-column:1 / 5;min-height:3.5em;font-family:Digital, sans-serif;text-align:end;margin:2px 0}#screen #total{color:orange;font-size:1.6em;overflow-wrap:break-word}#screen #display{color:white;font-size:1.7em}

button{color:white;border:none;outline:none;font-family:'Share Tech Mono', sans-serif;font-size:1.3em}button:active{box-shadow:0 0 2px 1px #fff}#clear{background:#ac3939;grid-column:1 / 3}#equals{background:#046;grid-column:4/5;grid-row:5/ 7}#zero{grid-column:1 / 3}.operator{background:#666}.number{background:#4d4d4d}

#credits{text-align:center;font-family:'Share Tech Mono', sans-serif}#built-by{color:#00264d;text-decoration:none}.unselectable{-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}

